:root {
    --font-color: rgb(25, 56, 88);
    --background-home: rgb(96, 205, 255);
    --background-panel: white;
    --hover-color: rgb(9, 113, 121);
}

body {
    /*     background-color: var(--background-home);
 */
    background-color: var(--background-home);
    color: var(--font-color);
}

.title-home {
    font-family: 'Verdana';
    font-size: 250%;
    padding-top: 6%;
}

.row {
    display: flex;
    flex-direction: row;
}

.portada {
    background-color: var(--background-panel);
    margin: 2%;
}

.column-title-home {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.column-menu-home {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.menu-home {
    font-family: 'Helvetica';
    width: 100%;
    background-color: var(--background-panel);
    margin-left: 2%;
    margin-right: 2%;
}

ul {
    list-style-type: none;
    font-size: 150%;
    margin-top: 0px;
}

.menu-home li {
    float: left;
    text-align: center;
    padding: 2% 5% 2% 5%;
}

.menu-home li:hover {
    background-color: var(--hover-color);
}

.column-photo-home {
    width: 40%;
}

.photo-home img {
    width: 60%;
    border-radius: 200px;
    padding: 20%;
}

.photo-home img:hover {
    background-color: var(--hover-color);
}

.two-cols{
    column-count: 2;
    column-gap: 5%;

}

.panel{
    margin: 2% 2% 2% 2%;
    font-size: 20px;
    background-color: var(--background-panel);
    padding: 2% 4% 2% 4%;
}

.panel p{
    text-align: justify;

}